import logo from './logo.svg';
import './App.css';
import React, { Component, useState, useEffect } from 'react';
import {Term, Text} from './term.js'
import {FlashcardsGame} from './flashcards'
import {WriteGame} from './writeGame'
import {LearnScreen, PracticeScreen} from './screens'


const GameStates = Object.freeze({
  LEARN_OR_PRACTICE: 0,
  LEARN: 1,
  PRACTICE: 2,
  FLASHCARDS: 3,
  WRITE: 4
});


export class App extends React.Component
{
  constructor(props)
  {
    super(props)
    this.state = {
            cardAmount: 10,
            startIndex: 1,
            game: GameStates.LEARN_OR_PRACTICE} 
    this.handleSubmit = this.handleSubmit.bind(this);

  }
  handleSubmit(event)
  {
    event.preventDefault()

    this.setState({cardAmount: event.target.amount.value ? parseInt(event.target.amount.value) : event.target.all.checked ? -1 : 10}) //do the value provided, or -1 (all flashcards) if "all cards" was checked, or 10 if no input at all
    this.setState({startIndex: event.target.index ? 
                      event.target.index.value ? 
                        parseInt(event.target.index.value) : 
                        1 :
                      1})
    this.setState({shuffle: event.target.randomize.checked })
    this.setState({game: GameStates.FLASHCARDS})
    //console.log(event.target.date.value)
    this.setState({dates: event.target.date ?  //only really relevant for Practice Game, where dates are used to seed the RNG
                            event.target.date.value ? 
                              event.target.date.value : 
                              "": 
                            ""})
    //this.setState({game: event.target.write.checked ? GameStates.WRITE : GameStates.FLASHCARDS}) <-- uncomment for write game
    this.setState({charOnly: event.target.charOnly.checked})
    let levels = []

    for (let i =1; i <= 6; ++i)
    {
      if (event.target["HSK" + i].checked)
      {
        levels.push(i)
      }
    }
    this.setState({levels: levels.length > 0 ? levels : [1]})
  }
  FirstScreen(props)
  {
    return (
      <div>
        <button className="choice" onClick={(e) => props.state.setState({game: GameStates.LEARN, practice:false})}>
        LEARN
        </button>
        <button className="choice" onClick={(e) => props.state.setState({game: GameStates.PRACTICE, practice:true})}>
        PRACTICE
        </button>
      </div>)
  }
  render()
  {
    let game = undefined

    switch (this.state.game)
    {
      case GameStates.FLASHCARDS:
        game = <FlashcardsGame
        cardAmount={this.state.cardAmount} 
        startIndex = {this.state.startIndex}
        shuffle = {this.state.shuffle}
        charOnly = {this.state.charOnly}
        levels = {this.state.levels}
        dates = {this.state.dates.split(", ")}
        practice = {this.state.practice}
        />
        break;
      case GameStates.WRITE:
        game = <WriteGame link={this.state.link}  cardAmount={this.state.cardAmount} startIndex = {this.state.startIndex}/>
        break;
      case GameStates.LEARN:
        game = <LearnScreen handleSubmit={this.handleSubmit}/>
        break;
      case GameStates.PRACTICE:
        game = <PracticeScreen handleSubmit={this.handleSubmit}/>
        break;
      case GameStates.LEARN_OR_PRACTICE:
      default:
        game = <this.FirstScreen state={this}/>
        break;

    }
    return (  
    <div  className="App">
      <header className="App-header">
      {game}
      </header>
    </div>
    )
  }
}

